export default {
	data() {
		return {
			phonemsg:'',
			codemsg:'',
			passwordmsg:'',
			passwordmsg2:'',
			isok:false,
			istrue:false,
			isright:false
		}
	},
	methods: {
		/* 手机号 */
		phone(value){
			let phoneReg=/^1[3-9]\d{9}$/; 
			let self=this
			if(value.trim().length){
				if(phoneReg.test(value)){
					uni.request({
						url:'http://localhost:10086/user',
						data:{
							phone:value
						},
						success(res) {
							console.log(res.data.code)
							if(res.data.code){
								uni.showToast({
									title:'该账号已注册',
									icon:'none'
								})
							}else{
								// uni.showToast({
								// 	title:'请发送验证码',
								// 	icon:'none'
								// })
								/* 可以注册 */
								self.isok=true
							}
						}
					})
				}else{
					uni.showToast({
							title:'手机号不正确',
							icon:'none'
						})
				}
			}else{
				uni.showToast({
					title:'手机号不能为空',
					icon:'none'
				})
			}
		},
		/* 验证码 */
		code(value){
			console.log(value)
			let codeReg=/^[0-9]{6}$/
			if(value.trim().length){
				if(codeReg.test(value.trim())){
					uni.showToast({
						title:"验证通过",
						icon:'none'
					})
				}else{
					uni.showToast({
						title:"验证码不合法",
						icon:'none'
					})
				}
			}else{
				uni.showToast({
					title:"验证码不能为空",
					icon:'none'
				})
			}
		},
		password(value){
			console.log(value)
			let passwordReg=/^[1-9a-zA-Z]{6,20}$/
			let self=this
			if(value.trim().length){
				if(passwordReg.test(value.trim())){
					/* 密码验证通过 */
					self.istrue=true
				}else{
					uni.showToast({
						title:'密码格式不正确',
						icon:'none'
					})
				}
			}else{
				uni.showToast({
					title:'密码不能为空',
					icon:'none'
				})
			}
		},
		password2(value){
			console.log(value)
			if(this.passwordmsg===value){
				this.isright=true
			}else{
				uni.showToast({
					title:'两次密码不一致',
					icon:'none'
				})
			}
		},
		reg(){
			console.log(22222)
			let self=this
			if(this.phonemsg.length && this.passwordmsg.length && this.passwordmsg2.length && this.codemsg.length){
				if(this.isok && this.istrue && this.isright){
					uni.setStorage({
						key:'phone',
						value:this.phonemsg.trim()
					}),
					uni.request({
						url:'http://localhost:10086/user/reg',
						method:'POST',
						data: {
						        phone: 'name',
						        password: '18'
						    },
					}),
					uni.reLaunch({
						url:'../login/login'
					})
				}else{
					uni.showToast({
						title:'手机号或密码不正确',
						icon:'none'
					})
				}
			}else{
				uni.showToast({
					title:'请完整填写信息',
					icon:'none'
				})
			}
		}
	}
}
